import React, { Component } from 'react';
import { Chart, Geom, Axis, Tooltip, Legend } from "bizcharts"
import { DataSet } from '@antv/data-set';
import NoData from '../../../../common/components/NoData/NoDataComponent';

// 
class LineChart extends Component {
    // 
    onFirstLoad(ev){
        if(this.props.onFirstLoad){
            this.props.onFirstLoad(ev);
        }
    }
    // 
    onClickAction(ev){
        if(this.props.onClickAction){
            this.props.onClickAction(ev);
        }
    }
    render() {
        /**
         * 
         * height  350
         * data  json
         * padding   [30, 20, 60, 50]
         * noLegend  LegendfalseLegend
         * colors   
         * geomType  Geomtype:
         *              point()
         *              area()
         * onFirstLoad  
         * onClickAction  
         * children  
         *
         * 2018/10/23
         * scales Chartscale
         * geomTooltip geom tooltip
         */

        const height = this.props.height || 350; // 
        const data = this.props.data; // 
        const ds = new DataSet(); // dateset
        const dv = ds.createView().source(data); // 
        let fields=[], nameKey; // 
        if (data && data[0]) {
            nameKey = Object.keys(data[0])[0]; // json
            // json
            Object.keys(data[0]).forEach((item,index)=>{
                if(index!==0){
                    fields.push(item);
                }
            })
        } else {
            // 
            return (<NoData height={height} data={'nodata'}/>);
        }
        // 
        dv.source(data).transform({
            type: 'fold',
            fields: fields.length>0?fields:['null'],
            key: 'type',
            value: 'value'
        });
        let scales = this.props.scales || {value:{min:0}};
        return (
            <Chart height={height} data={dv} scale={scales} padding={this.props.padding || [30, 20, 60, 50]} forceFit
                   onGetG2Instance={(ev)=>this.onFirstLoad(ev)}>
                {this.props.noLegend?null:<Legend marker={'circle'}/>}
                <Axis name={nameKey} />
                <Axis name="value" />
                <Tooltip crosshairs={{type : "y"}}/>
                <Geom
                    type="line"
                    position={nameKey+"*value"}
                    color={['type',this.props.colors]}  tooltip={this.props.geomTooltip}/>
                <Geom
                    type={this.props.geomType||'point'}
                    position={nameKey+"*value"}
                    shape={'circle'}
                    color={['type',this.props.colors]} />
                {this.props.children}
            </Chart>
        )
    }
}

export default LineChart;


